body {
  margin: 0;
  font-family:"Nunito" , -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#main h1{
  color: #444;
  text-align: center;
}

#main{
  padding: 0;
  box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#book-list{
  padding: 0;
}

#book-list li{
  display: inline-block;
  margin: 12px;
  padding: 10px 15px;
  border-radius: 4px;
  border: 1px solid #880E4f;
  box-shadow:1px 2px 3px rgba(0,0,0,0.3) ; 
  cursor: pointer;
  color :#880E4f;
}



form{
  margin: 0;
  padding: 0;
  background: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 400px
}

form .field{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;

  /* grid-template-rows: 1fr ; */
}

form label{
  text-align: right;
  padding: 6px;
}

form select , form input{
  margin: 4px;
  padding: 6px;
  box-sizing: border-box;
}


form button{
  color: #fff;
  font-size: 2em;
  background-color: #ad1457;
  border: 0;
  padding: 0 10px;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}

#book-details{
  position: fixed;
  top: 0%;
  right: 0;
  width: 40%;
  height: 100%;
  background-color: #ad1457 ;
  padding: 30px;
  overflow: auto;
  box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
  box-sizing: border-box;
  color: #fff;
}